.swiper {
  width: 100%;
  max-height: 300px; }
  .swiper-wrapper {
    padding-bottom: 25px; }
  .swiper-slide {
    display: flex !important;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
    min-width: 224px;
    max-width: 224px;
    min-height: 72px;
    max-height: 72px;
    background-color: #fff;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    margin-right: 24px; }
  .swiper-pagination {
    display: flex;
    justify-content: center; }

@media screen and (min-width: 768px) {
  .swiper {
    display: none !important; } }
