@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.ttf); }

.footer {
  display: flex;
  flex-direction: column;
  gap: 16px;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 105px;
  max-height: 147px;
  padding-top: 24px;
  padding-bottom: 16px;
  padding-left: 16px;
  background-color: #f8f8f8;
  border-top: 1px solid #41f6d7;
  color: #7e7e82; }
  .footer__privacy-policy {
    text-decoration: none;
    color: #7e7e82; }

@media screen and (min-width: 768px) {
  .footer {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding: 0px 24px; }
    .footer__information {
      text-align: right;
      max-width: 320px; } }

@media screen and (min-width: 1440px) {
  .footer {
    width: 100%;
    max-width: 1120px;
    margin-left: 320px; } }
