@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.ttf); }

body {
  font-family: 'TT Lakes Regular';
  margin: 0;
  box-sizing: border-box; }

.wrapper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto; }

.icon__back {
  cursor: pointer;
  min-width: 40px;
  min-height: 40px;
  border-radius: 20px;
  border: 0;
  background: #ff3e79; }

.icon__back:hover {
  opacity: 0.3; }

.service-more__text {
  font-family: 'TT Lakes Regular';
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1b1c21; }
