@charset "UTF-8";
@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.ttf); }

.service {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-top: 24px;
  padding-bottom: 16px;
  padding-left: 16px;
  background-color: #f8f8f8; }
  .service__title {
    font-family: 'TT Lakes Bold';
    font-size: 28px;
    line-height: 40px;
    letter-spacing: -0.6px;
    text-align: left;
    border-left: 4px solid #41f6d7;
    padding-left: 12px;
    margin-left: -16px; }
  .service__actions {
    display: none;
    list-style-type: none; }
  .service__repair {
    cursor: pointer;
    min-width: 40px;
    min-height: 40px;
    border-radius: 20px;
    border: 0; }
  .service__repair::before {
    position: absolute;
    margin-left: -140px;
    content: 'Оставить заявку'; }
  .service__repair::before:hover {
    color: #ff3e79; }
  .service__checkstatus {
    cursor: pointer;
    min-width: 40px;
    min-height: 40px;
    border-radius: 20px;
    border: 0; }
  .service__checkstatus::before {
    position: absolute;
    margin-left: -140px;
    content: 'Статус ремонта'; }
  .service__image {
    min-width: 320px;
    margin-left: -16px; }
  .service__info {
    min-width: 304px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap; }
  .service__text {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    text-align: left;
    min-width: 304px;
    margin-bottom: 20px; }
  .service__paragraph {
    overflow-y: hidden;
    min-width: 288px;
    max-width: 490px;
    margin-bottom: 16px;
    max-height: 150px;
    transition: 0.7s; }
  .service__paragraph--open {
    max-height: 400px;
    transition: 0.7s; }
  .service__more {
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    padding: 5px;
    margin-left: 32px;
    border: none;
    border-color: rgba(0, 0, 0, 0);
    background-color: rgba(0, 0, 0, 0);
    color: #1b1c21; }
  .service__more:hover {
    text-shadow: 0 0 10px #7e7e82;
    color: #ff3e79; }
  .service__more::before {
    position: absolute;
    margin-left: -20px;
    content: url(./static/icon-more.svg); }
  .service__more--open::before {
    transform: rotate(180deg); }
  .service__list {
    min-height: 60px;
    padding-left: 0;
    list-style: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
    overflow-x: scroll;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    gap: 10px; }
  .service__list::-webkit-scrollbar {
    width: 0; }
  .service__link {
    color: #7e7e82;
    white-space: nowrap;
    text-decoration: none;
    background: #f8f8f8;
    border: 2px solid #f8f8f8;
    border-radius: 6px;
    padding: 8px 10px; }
  .service__link--active {
    background: #fff;
    border: 2px solid #41f6d7; }
  .service__reviews {
    display: none; }

@media screen and (min-width: 768px) {
  .service {
    padding: 24px; }
    .service__title {
      padding-left: 20px !important;
      margin-left: -24px !important; }
    .service__info {
      flex-wrap: nowrap; }
    .service__image {
      width: 100%;
      max-width: 360px;
      margin: 0; }
    .service__text {
      min-width: 312px; } }

@media screen and (min-width: 1440px) {
  .service {
    padding: 24px;
    width: 100%;
    max-width: 1120px;
    min-height: 535px;
    margin-left: 320px; }
    .service__actions {
      margin-left: auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 140px; }
    .service__container {
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-between;
      align-items: center; }
    .service__reviews {
      display: block;
      text-decoration: none;
      margin-left: 32px;
      color: #7e7e82; }
    .service__list {
      overflow: hidden; }
    .service__text {
      min-width: 490px; }
    .service__image {
      max-width: 531px;
      max-height: 307px;
      margin: 0; }
    .service__info {
      min-width: 1120px; } }
