@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.ttf); }

.service-devices {
  max-width: 1120px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin: 0;
  margin-top: 16px; }
  .service-devices__title {
    font-size: 16px;
    line-height: 24px;
    color: #7e7e82;
    text-transform: uppercase;
    background-color: #fff;
    padding: 24px;
    margin-left: -24px;
    border-top: 1px solid #41f6d7; }
  .service-devices__blocks {
    display: none; }
  .service-devices__block {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
    min-width: 224px;
    min-height: 72px;
    background-color: #fff;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    margin-top: 16px;
    margin-left: 24px; }
  .service-devices__block:hover {
    box-shadow: 0 0 10px #7e7e82; }
  .service-devices__image {
    margin-left: 16px; }
  .service-devices__next {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 40px;
    min-height: 40px;
    border: 2px solid #ff3e79;
    border-radius: 50%;
    margin-right: 16px;
    background-color: #fff; }
  .service-devices__next:hover {
    box-shadow: 0 0 10px #7e7e82; }
  .service-devices__more {
    display: none; }
  .service-devices__more:hover {
    text-shadow: 0 0 10px #7e7e82;
    color: #ff3e79; }

@media screen and (min-width: 768px) {
  .service-devices__title {
    margin-right: -24px; }
  .service-devices__swiper {
    display: none; }
  .service-devices__blocks {
    max-height: 190px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    transition: 0.7s; }
  .service-devices__blocks--open {
    min-height: 250px;
    max-height: 550px;
    height: auto;
    transition: 0.7s;
    padding-bottom: 16px; }
  .service-devices__more {
    cursor: pointer;
    display: inline-block;
    max-width: 120px;
    font-size: 16px;
    padding: 5px;
    margin-left: 32px;
    border: none;
    border-color: rgba(0, 0, 0, 0);
    background-color: rgba(0, 0, 0, 0);
    color: #1b1c21; }
  .service-devices__more::before {
    position: absolute;
    margin-left: -20px;
    content: url(./static/icon-more.svg);
    transform: rotate(0deg); }
  .service-devices__more--open::before {
    position: absolute;
    margin-left: -20px;
    content: url(./static/icon-more.svg);
    transform: rotate(180deg); } }
