@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.ttf); }

.service-prices {
  max-width: 1120px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin: 0;
  margin-top: 16px; }
  .service-prices__title {
    font-size: 16px;
    line-height: 24px;
    color: #7e7e82;
    text-transform: uppercase;
    background-color: #fff;
    padding: 24px;
    margin-left: -24px;
    border-top: 1px solid #41f6d7; }
  .service-prices__blocks {
    display: none; }
  .service-prices__block {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 16px 24px;
    background-color: #fff; }
  .service-prices__button {
    position: absolute;
    bottom: 16px;
    right: 16px;
    min-width: 119px;
    min-height: 32px;
    border-radius: 16px;
    border: #ff3e79;
    background-color: #ff3e79;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    padding: 4px 12px; }
  .service-prices__button:hover {
    box-shadow: 0 0 10px #7e7e82; }
  .service-prices__span {
    color: #fff;
    font-family: 'TT Lakes Bold';
    font-size: 12px;
    line-height: 24px;
    letter-spacing: -0.2px;
    text-transform: uppercase; }
  .service-prices__slide {
    position: relative;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: start;
    align-items: start;
    min-width: 260px;
    min-height: 200px;
    background-color: #fff;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    margin-top: 16px;
    margin-left: 24px;
    padding: 16px;
    gap: 16px;
    white-space: normal; }
  .service-prices__services-title {
    color: #7e7e82;
    font-family: 'TT Lakes Bold';
    opacity: 72.18%;
    margin-bottom: 4px; }
  .service-prices__services {
    white-space: normal; }
  .service-prices__price-info {
    min-width: 288px;
    max-width: 421px;
    font-size: 14px; }
  .service-prices__offer {
    position: relative;
    text-decoration: none;
    font-family: 'TT Lakes Bold';
    font-size: 16px;
    line-height: 24px; }
  .service-prices__offer:hover {
    text-shadow: 0 0 10px #7e7e82;
    color: #ff3e79; }
  .service-prices__offer::after {
    position: absolute;
    margin-left: 10px;
    top: auto;
    content: url(./static/dropdown.svg); }

.block-title {
  background: #f8f8f8;
  color: #7e7e82;
  font-family: 'TT Lakes Bold';
  opacity: 72.18%; }

@media screen and (min-width: 768px) {
  .service-prices__title {
    margin-right: -24px; }
  .service-prices__blocks {
    display: flex;
    flex-direction: column;
    gap: 2px; }
  .service-prices__button {
    position: relative;
    bottom: auto;
    right: auto; }
  .service-prices__services {
    min-width: 388px; }
  .service-prices__price {
    min-width: 90px; }
  .service-prices__time {
    min-width: 95px; }
  .service-prices__order {
    min-width: 119px; } }
