@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.ttf); }

.service-advantages {
  max-width: 1120px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin: 0;
  margin-top: 16px; }
  .service-advantages__title {
    font-size: 16px;
    line-height: 24px;
    color: #7e7e82;
    text-transform: uppercase;
    background-color: #fff;
    padding: 24px;
    margin-left: -24px;
    border-top: 1px solid #41f6d7; }
  .service-advantages__blocks {
    display: none; }
  .service-advantages__block {
    position: relative;
    box-sizing: border-box;
    min-width: 224px;
    min-height: 160px;
    background-color: #fff;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    margin-top: 16px;
    margin-left: 24px;
    padding: 16px; }
  .service-advantages__block:hover {
    box-shadow: 0 0 10px #7e7e82; }
  .service-advantages__image {
    margin-left: 16px; }
  .service-advantages__next {
    cursor: pointer;
    position: absolute;
    bottom: 16px;
    right: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 40px;
    min-height: 40px;
    border: 2px solid #ff3e79;
    border-radius: 50%;
    margin-right: 16px;
    background-color: #fff; }
  .service-advantages__next:hover {
    box-shadow: 0 0 10px #7e7e82; }
  .service-advantages__more {
    display: none; }
  .service-advantages__more:hover {
    text-shadow: 0 0 10px #7e7e82;
    color: #ff3e79; }
  .service-advantages__slide {
    position: relative;
    box-sizing: border-box;
    align-items: start;
    min-width: 240px;
    min-height: 160px;
    background-color: #fff;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    margin-top: 16px;
    margin-left: 24px;
    padding: 16px; }

@media screen and (min-width: 768px) {
  .service-advantages__title {
    margin-right: -24px; }
  .service-advantages__blocks {
    max-height: 170px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    transition: 0.7s linear;
    padding-bottom: 16px; }
  .service-advantages__blocks--open {
    min-height: 250px;
    max-height: 550px;
    height: auto;
    padding-bottom: 16px;
    transition: 0.7s linear; }
  .service-advantages__more {
    cursor: pointer;
    display: inline-block;
    max-width: 120px;
    font-size: 16px;
    padding: 5px;
    margin-left: 32px;
    border: none;
    border-color: rgba(0, 0, 0, 0);
    background-color: rgba(0, 0, 0, 0);
    color: #1b1c21; }
  .service-advantages__more::before {
    position: absolute;
    margin-left: -20px;
    content: url(./static/icon-more.svg);
    transform: rotate(0deg); }
  .service-advantages__more--open::before {
    position: absolute;
    margin-left: -20px;
    content: url(./static/icon-more.svg);
    transform: rotate(180deg); } }

@media screen and (min-width: 1057px) {
  .service-advantages__more {
    display: none; } }
