@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.ttf); }

.modal-feedback {
  position: fixed;
  right: -100%;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  height: 100%;
  background-color: #fff;
  border-bottom: 1px solid #41f6d7;
  z-index: 150;
  transition: 0.7s; }
  .modal-feedback__wrapper {
    visibility: hidden;
    opacity: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 200;
    margin: 0 auto; }
  .modal-feedback__wrapper--show {
    visibility: visible;
    opacity: 1; }
  .modal-feedback__overlay {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    opacity: 0.92; }
  .modal-feedback__burger {
    position: relative;
    left: 0;
    margin-left: 16px;
    margin-top: 16px;
    max-width: 40px;
    max-height: 40px; }
  .modal-feedback__title {
    font-family: 'TT Lakes Bold';
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    text-align: left;
    border-left: 4px solid #41f6d7;
    padding-left: 28px;
    margin-left: -32px; }
  .modal-feedback__body {
    position: relative;
    top: 0;
    right: 0;
    padding: 32px;
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: start;
    gap: 16px;
    overflow-y: auto; }
  .modal-feedback__form {
    display: flex;
    flex-direction: column;
    gap: 16px; }
  .modal-feedback__form-style {
    box-sizing: border-box;
    min-height: 48px;
    padding: 12px 16px;
    border: 1px solid #eaeaea;
    border-radius: 8px; }
  .modal-feedback__text {
    box-sizing: border-box;
    min-height: 118px;
    padding: 12px 16px;
    border: 1px solid #eaeaea;
    border-radius: 8px; }
  .modal-feedback__span {
    color: #ff3e79; }
  .modal-feedback__submit {
    position: absolute;
    top: 0px;
    right: 0px;
    min-width: 119px;
    min-height: 32px;
    border-radius: 16px;
    border: #ff3e79;
    background-color: #ff3e79;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    padding: 4px 12px; }
  .modal-feedback__submit-wrapper {
    position: relative; }
  .modal-feedback__submit:hover {
    box-shadow: 0 0 10px #7e7e82; }

.modal-feedback--show {
  right: 0;
  transition: 0.7s; }

@media screen and (min-width: 768px) {
  .modal-feedback {
    max-width: 320px;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); }
    .modal-feedback__burger {
      position: absolute;
      left: -80px; } }
