@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.ttf); }

.header {
  margin: 0;
  width: 100%;
  min-height: 87px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  border-bottom: 1px solid #41f6d7; }
  .header__burger {
    margin-left: 16px;
    margin-right: 16px; }
  .header__logo {
    cursor: pointer;
    top: 3.69%;
    bottom: 86.23%; }
  .header__actions {
    list-style-type: none;
    margin-left: auto;
    padding: 0 !important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px; }
  .header__divider {
    cursor: pointer;
    min-width: 2px;
    min-height: 32px;
    background: #eaeaea;
    border-radius: 1px;
    border: 0; }
  .header__checkstatus {
    margin-right: 16px; }
  .header__actions li:nth-child(1) {
    display: none; }
  .header__actions li:nth-child(2) {
    display: none; }
  .header__actions li:nth-child(3) {
    display: none; }

/********************************************768px******************************/
@media screen and (min-width: 768px) {
  .header {
    width: 100%; }
    .header__actions li:nth-child(1) {
      display: block; }
    .header__actions li:nth-child(2) {
      display: block; }
    .header__actions li:nth-child(3) {
      display: block; }
    .header__burger {
      margin-left: 24px;
      margin-right: 47px; }
    .header__burger::after {
      display: inline-block;
      position: absolute;
      margin-left: 30px;
      margin-top: -5px;
      content: url(./static/icon-divider.svg); }
    .header__checkstatus {
      margin-right: 40px; } }

/*******************************************1440px******************************/
@media screen and (min-width: 1440px) {
  .header {
    display: none; } }
