@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.ttf); }

.aside-menu {
  position: fixed;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  height: 100%;
  background-color: #fff;
  border-bottom: 1px solid #41f6d7;
  z-index: 150;
  transition: 0.7s; }
  .aside-menu__wrapper {
    visibility: hidden;
    opacity: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    top: -100%;
    left: 0;
    z-index: 200;
    margin: 0 auto;
    transition: 0.7s; }
  .aside-menu__wrapper--show {
    visibility: visible;
    opacity: 1;
    top: 0; }
  .aside-menu__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    opacity: 0.92; }
  .aside-menu__header {
    position: fixed;
    top: 0;
    width: 100%;
    min-width: 320px;
    min-height: 87px;
    display: flex;
    align-items: center;
    flex-direction: row; }
  .aside-menu__burger {
    margin-left: 24px;
    margin-right: 16px; }
  .aside-menu__search {
    margin-right: 24px;
    margin-left: auto; }
  .aside-menu__body {
    position: relative;
    top: 0;
    margin-top: 87px;
    padding-top: 20px;
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    overflow-y: auto; }
  .aside-menu__list {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px; }
  .aside-menu__link {
    color: #1b1c21;
    white-space: nowrap;
    text-decoration: none;
    border-left: 4px solid #fff;
    padding: 8px 10px;
    padding-left: 24px;
    margin-left: -40px; }
  .aside-menu__link:hover {
    color: #ff3e79; }
  .aside-menu__link--active {
    border-left: 4px solid #41f6d7; }
  .aside-menu__info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-width: 320px;
    margin-top: auto;
    margin-bottom: 32px; }
  .aside-menu__actions {
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    gap: 16px; }
  .aside-menu__call {
    margin-left: 24px; }
  .aside-menu__contacts {
    min-height: 70px;
    padding-left: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 12px; }
  .aside-menu__mail {
    cursor: pointer;
    text-decoration: none;
    font-size: 16px;
    line-height: 24px;
    text-align: left;
    color: #1b1c21; }
  .aside-menu__mail:hover {
    color: #ff3e79; }
  .aside-menu__phone-number {
    cursor: pointer;
    text-decoration: none;
    font-family: 'TT Lakes Bold';
    font-size: 24px;
    line-height: 32px;
    text-align: left;
    color: #1b1c21; }
  .aside-menu__phone-number:hover {
    color: #ff3e79; }
  .aside-menu__languages {
    padding-left: 24px;
    margin-top: 40px;
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    gap: 7px; }
  .aside-menu__label_RU {
    cursor: pointer;
    color: #1b1c21; }
  .aside-menu__label_RU:hover {
    color: #ff3e79; }
  .aside-menu__label_RU--active {
    color: #7e7e82; }
  .aside-menu__lang_RU {
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    appearance: none; }
  .aside-menu__label_ENG {
    cursor: pointer;
    color: #1b1c21; }
  .aside-menu_label_ENG:hover {
    color: #ff3e79; }
  .aside-menu__label_ENG--active {
    color: #7e7e82; }
  .aside-menu__lang_ENG {
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    appearance: none; }
  .aside-menu__label_CH {
    cursor: pointer;
    color: #1b1c21; }
  .aside-menu__label_CH:hover {
    color: #ff3e79; }
  .aside-menu__label_CH--active {
    color: #7e7e82; }
  .aside-menu__lang_CH {
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    appearance: none; }

@media screen and (min-width: 768px) {
  .aside-menu {
    max-width: 320px;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); }
    .aside-menu__header {
      max-width: 320px; } }

@media screen and (min-width: 1440px) {
  .aside-menu {
    box-shadow: none; }
    .aside-menu__wrapper {
      visibility: visible;
      opacity: 1;
      position: sticky;
      top: 0;
      left: 0;
      margin: 0;
      min-width: 320px;
      z-index: 1; }
    .aside-menu__overlay {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 0; }
    .aside-menu__burger {
      display: none; }
    .aside-menu__logo {
      margin-left: 24px; } }
